@charset "UTF-8";
/* reset.css - 레진 */
body {
  position: relative;
  margin: 0;
  overflow-y: scroll;
  background: var(--b_100);
  color: var(--t_200);
  font-family: Helvetica, Arial, sans-serif;
  font-size: 14px;
  line-height: 1.5;
  -webkit-text-size-adjust: none;
  text-size-adjust: none;
  word-wrap: break-word;
  overflow-wrap: break-word; }

a {
  color: inherit;
  text-decoration: none; }

b {
  color: var(--t_red); }

p {
  margin: 0; }

img, dialog {
  border: 0; }

dialog::backdrop {
  display: none; }

[data-srcset], [data-src] {
  opacity: 0;
  min-width: 1px;
  min-height: 1px; }

table {
  border-collapse: collapse; }

main, dialog {
  display: block; }

ol, ul {
  margin: 0;
  padding: 0;
  list-style: none; }

[hidden], template {
  display: none; }

button, input, textarea {
  margin: 0;
  padding: 0;
  color: inherit;
  font: inherit; }

h1, h2, h3, h4, h5 {
  font-weight: normal; }

@media (prefers-color-scheme: dark) {
  button:-webkit-autofill, button:-webkit-autofill:hover, button:-webkit-autofill:focus, button:-webkit-autofill:active, input:-webkit-autofill, input:-webkit-autofill:hover, input:-webkit-autofill:focus, input:-webkit-autofill:active, textarea:-webkit-autofill, textarea:-webkit-autofill:hover, textarea:-webkit-autofill:focus, textarea:-webkit-autofill:active {
    -webkit-box-shadow: 0 0 0 1000px var(--b_100) inset;
    -webkit-text-fill-color: var(--t_100); } }

textarea {
  resize: vertical; }

input, textarea, [type='text'], [type='email'], [type='password'], [type='search'], [type='tel'], [type='number'] {
  border: 1px solid var(--d_300);
  border-radius: 4px;
  -webkit-appearance: none;
  -moz-appearance: none; }

[type='search']::-ms-clear, [type='search']::-ms-reveal {
  display: none; }

[type='search']::-webkit-search-decoration, [type='search']::-webkit-search-cancel-button, [type='search']::-webkit-search-results-button, [type='search']::-webkit-search-results-decoration {
  display: none; }

::placeholder {
  color: rgba(0, 0, 0, 0.5); }

@media (prefers-color-scheme: dark) {
  ::placeholder {
    color: rgba(255, 255, 255, 0.3); } }

fieldset, legend {
  margin: 0;
  padding: 0;
  border: 0; }

button, [type='button'], [type='submit'], [type='reset'] {
  border: 0;
  background: none; }

input[type='submit'], input[type='image'], label[for], select, button {
  cursor: pointer; }

.offscreen {
  overflow: hidden;
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  line-height: 0;
  margin: -1px; }

iframe {
  display: none; }
